  // ÿØÿßŸÑÿ© ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ
  async login(username: string, password: string): Promise<ApiResponse<LoginResponse>> {
    console.log(`üîë ŸÖÿ≠ÿßŸàŸÑÿ© ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ ŸÑŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ: ${username}`);
    
    try {
      const response = await this.request<LoginResponse>("auth/login", {
        method: "POST",
        body: JSON.stringify({ username, password }),
      });

      if (response.success && response.data) {
        // ÿ≠ŸÅÿ∏ ÿ±ŸÖÿ≤ ÿßŸÑŸàÿµŸàŸÑ ŸÅŸä ÿßŸÑÿ™ÿÆÿ≤ŸäŸÜ ÿßŸÑŸÖÿ≠ŸÑŸä
        if (typeof window !== "undefined") {
          localStorage.setItem("auth_token", response.data.token);
          localStorage.setItem("user_info", JSON.stringify(response.data.user));
        }
        console.log("‚úÖ ÿ™ŸÖ ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ ÿ®ŸÜÿ¨ÿßÿ≠");
        return response;
      } else {
        console.error("‚ùå ŸÅÿ¥ŸÑ ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ:", response.error);
        return response;
      }
    } catch (error) {
      console.error("‚ùå ÿÆÿ∑ÿ£ ÿ£ÿ´ŸÜÿßÿ° ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ:", error);
      return this.getMockLoginResponse(username);
    }
  }

  // ÿ®ŸäÿßŸÜÿßÿ™ ŸàŸáŸÖŸäÿ© ŸÑÿßÿ≥ÿ™ÿ¨ÿßÿ®ÿ© ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ
  private getMockLoginResponse(username: string): ApiResponse<LoginResponse> {
    // ÿßÿ≥ÿ™ÿÆÿØÿßŸÖ ÿ®ŸäÿßŸÜÿßÿ™ ŸàŸáŸÖŸäÿ© ŸÅŸä ÿ≠ÿßŸÑÿ© ŸÅÿ¥ŸÑ ÿßŸÑÿßÿ™ÿµÿßŸÑ ÿ®ÿßŸÑÿÆÿßÿØŸÖ
    if (username === "admin" || username === "test") {
      return {
        success: true,
        data: {
          token: "mock_token_12345",
          user: {
            id: 1,
            username: username,
            fullName: username === "admin" ? "ŸÖÿØŸäÿ± ÿßŸÑŸÜÿ∏ÿßŸÖ" : "ŸÖÿ≥ÿ™ÿÆÿØŸÖ ÿ™ÿ¨ÿ±Ÿäÿ®Ÿä",
            email: `${username}@example.com`,
            role: username === "admin" ? "admin" : "user",
          },
        },
      };
    } else {
      return {
        success: false,
        error: "ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ÿ£Ÿà ŸÉŸÑŸÖÿ© ÿßŸÑŸÖÿ±Ÿàÿ± ÿ∫Ÿäÿ± ÿµÿ≠Ÿäÿ≠ÿ©"
      };
    }
  }
